<template>
  <div id="novel-details">
    <div class="breadcrumb-box">
      <Breadcrumb>
        <Breadcrumb-item href="javascript:;" @click.native="routeTo('/home')"> 首页 </Breadcrumb-item>
        <Breadcrumb-item href="javascript:;" @click.native="routeTo('/novel')"> 小说管理 </Breadcrumb-item>
        <Breadcrumb-item> 编辑小说 </Breadcrumb-item>
      </Breadcrumb>
    </div>
    <Row>
      <Card class="table-box">
        <h2 style="text-align: left;">编辑小说</h2>
        <hr>
        <br>
        <i-form ref="formItem" :model="formItem" :label-width="100" :rules="ruleFormItem">
          <Form-item label="书名(英文)" prop="title">
            <i-input v-model="formItem.title" placeholder="请输入英文名称..." style="width: 500px;"></i-input>
          </Form-item>
          <Form-item label="书名(中文)" prop="chinese_title">
            <i-input v-model="formItem.chinese_title" placeholder="请输入中文名称..." style="width: 500px;"></i-input>
          </Form-item>
          <Form-item label="简介" prop="intro">
            <i-input v-model="formItem.intro" type="textarea" :autosize="{ minRows: 6, maxRows: 8 }" placeholder="简介内容将在首页部分地方显示..."></i-input>
          </Form-item>
          <Form-item label="分类标签" prop="sort_id">
            <Select v-model="formItem.sort_id" placeholder="选择分类" style="width: 300px;">
              <Option v-for="item in sort_list" :value="item.id">{{ item.chinese_name }}</Option>
            </Select>
          </Form-item>
          <Form-item label="作者" prop="author">
            <i-input v-model="formItem.author" placeholder="不填写默认为unknown" style="width: 200px;"></i-input>
          </Form-item>
          <Form-item label="发布状态" prop="publish_status">
            <Radio-group v-model="formItem.publish_status" type="button">
              <Radio label="0">连载中</Radio>
              <Radio label="1">已完结</Radio>
            </Radio-group>
          </Form-item>
          <Form-item label="评级">
            <Row>
              <i-col span="24">
                <Rate allow-half show-text v-model="formItem.rate">
                  <span style="color: #f5a623">{{ formItem.rate }}</span>
                </Rate>
              </i-col>
              <i-col span="6">
                <Slider v-model="formItem.rate" show-input :max="5" :min="1"></Slider>
              </i-col>
            </Row>
          </Form-item>
          <Form-item label="封面" prop="cover_ext">
            <div class="demo-upload-list" v-for="item in defaultList">
              <template v-if="item.status === 'finished'">
                <img :src="item.url">
                <div class="demo-upload-list-cover">
                  <Icon type="ios-eye-outline" @click.native="handleView(item.url)"></Icon>
                  <Icon type="ios-trash-outline" @click.native="handleRemove(item)"></Icon>
                </div>
              </template>
              <template v-else>
                <Progress v-if="item.showProgress" :percent="item.percentage" hide-info></Progress>
              </template>
            </div>
            <Upload
              ref:upload
              :on-success="handleSuccess"
              :show-upload-list="false"
              :default-file-list="defaultList"
              :format="['jpg','jpeg','png']"
              :max-size="2048"
              :on-format-error="handleFormatError"
              :on-exceeded-size="handleMaxSize"
              :before-upload="handleBeforeUpload"
              type="drag"
              action="/admin_api/novel/cover"
              :headers="cover_header"
              name="cover"
              style="display: inline-block;width:auto;">
              <div style="width: 90px;height: 120px;line-height: 120px;">
                <Icon type="camera" size="20"></Icon>
              </div>
            </Upload>
            <Modal title="查看图片" v-model="showCover">
              <img :src="imgUrl" v-if="showCover" style="width: 100%">
            </Modal>
          </Form-item>
          <Form-item>
            <i-button type="primary" @click.native="handleSubmit('formItem')"> 提交修改 </i-button>
            <i-button type="ghost" @click.native="routeTo('/novel')" style="margin-right: 10px"> 取消返回 </i-button>
          </Form-item>
        </i-form>
      </Card>
    </Row>
  </div>
</template>

<script>
  export default {
    data () {
      return {
        showCover: false,
        imgUrl: '',
        defaultList: [],
        cover_header: {
          'Authorization': ''
        },
        formItem: {
          title: '',
          chinese_title: '',
          intro: '',
          sort_id: 0,
          rate: 5,
          author: '',
          cover_ext: '',
          cover_name: '',
          publish_status: 0
        },
        sort_list: [],
        ruleFormItem: {
          title: [
            { required: true, message: '英文名称不能为空', trigger: 'blur' },
            { type: 'string', max: 100, message: '书名太长了', trigger: 'blur' }
          ],
          chinese_title: [
            { required: true, message: '中文名称不能为空', trigger: 'blur' },
            { type: 'string', max: 50, message: '书名太长了', trigger: 'blur' }
          ],
          intro: [
            { type: 'string', max: 500, message: '简介不能超过500个字符', trigger: 'blur' }
          ],
          author: [
            { type: 'string', max: 50, message: '作者名称太长', trigger: 'blur' }
          ]
        }
      }
    },
    created () {
      this.initList()
      this.cover_header.Authorization = sessionStorage.getItem('access_token', null)
    },
    methods: {
      routeTo (s) {
        return this.$router.push(s)
      },
      handleSubmit (name) {
        this.$refs[name].validate((valid) => {
          if (valid) {
            this.$http.post('/admin_api/novel/edit', this.formItem).then(response => {
              if (response.body.error === 0) {
                this.$Message.success(response.body.message)
                this.$router.push('/novel')
              } else {
                this.$Notice.error({ title: '错误编号:' + response.error, desc: response.message })
              }
            })
          } else {
            this.$Notice.error({ title: '提交的数据有误', desc: '你是不是对提交的信息做手脚了?!' })
          }
        })
      },
      handleView (url) {
        this.imgUrl = url
        this.showCover = true
      },
      handleRemove () {
        this.defaultList = []
        this.formItem.cover_ext = ''
      },
      handleSuccess (response) {
        this.defaultList.push(response.data)
        this.formItem.cover_ext = this.defaultList[0].cover_ext
        this.formItem.cover_name = this.defaultList[0].name
        this.defaultList[0].status = 'finished'
      },
      handleFormatError (file) {
        this.$Notice.warning({
          title: '文件格式不正确',
          desc: '文件 ' + file.name + ' 格式不正确，请上传 jpg 或 png 格式的图片。'
        })
      },
      handleMaxSize (file) {
        this.$Notice.warning({
          title: '超出文件大小限制',
          desc: '文件 ' + file.name + ' 太大，不能超过 2M。'
        })
      },
      handleBeforeUpload () {
        const check = this.defaultList.length < 1
        if (!check) {
          this.$Notice.warning({
            title: '最多只能上传 1 张图片。'
          })
        }
        return check
      },
      initList () {
        let id = this.$route.query.id || ''
        if (id === '') {
          this.$Notice.error({ title: '页面初始化失败', desc: '缺少必要的文章编号' })
          return this.$router.push('/novel')
        }
        this.$http.post('/admin_api/novel/edit', { 'get': 'one', 'id': id }).then(response => {
          if (response.body.error === 0) {
            this.formItem = response.body.data.novel
            this.sort_list = response.body.data.sort_list
            if (response.body.data.novel.cover_url !== '') {
              this.defaultList.push({
                name: response.body.data.novel.id,
                url: response.body.data.novel.cover_url,
                status: 'finished'
              })
            }
          } else {
            this.$Notice.error({ title: '初始化数据失败', desc: response.body.message })
          }
        })
      }
    }
  }
</script>

<style>
  .demo-upload-list{
    display: inline-block;
    width: 90px;
    height: 120px;
    text-align: center;
    line-height: 120px;
    border: 1px solid transparent;
    border-radius: 4px;
    overflow: hidden;
    background: #fff;
    position: relative;
    box-shadow: 0 1px 1px rgba(0,0,0,.2);
    margin-right: 4px;
  }
  .demo-upload-list img{
    width: 100%;
    height: 100%;
  }
  .demo-upload-list-cover{
    display: none;
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    background: rgba(0,0,0,.6);
  }
  .demo-upload-list:hover .demo-upload-list-cover{
    display: block;
  }
  .demo-upload-list-cover i{
    color: #fff;
    font-size: 20px;
    cursor: pointer;
    margin: 0 2px;
  }
</style>



